<template>
  <v-container>
      <v-btn to="/home">Home</v-btn>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <h4>Request Callout Checkup</h4>
      </v-flex>
    </v-layout>
    <v-layout row>
      <v-flex xs12>
        <form @submit.prevent="onCreateCheckup">
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-select
              v-bind:items="itemsStock"
                name="stockName"
                label="Choose your livestock"
                v-model="stockName"
                required></v-select>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-select
              v-bind:items="itemsVetName"
                name="vetName"
                label="Choose your vet"
                v-model="vetName"
                required></v-select>
            </v-flex>
          </v-layout>
          <v-layout row>
               <v-flex xs12 sm6 offset-sm3>
                 <h4>Choose Date</h4>
               </v-flex>
             </v-layout>
             <v-layout row class="mr-2">
                  <v-flex xs12 sm6 offset-sm3 >
                    <v-date-picker v-model="date"></v-date-picker>
                    <p> {{date}} </p>
                  </v-flex>
                  <v-flex xs12 sm6 offset-sm3>
                    <v-time-picker v-model="time"></v-time-picker>
                    <p> {{time}} </p>
                  </v-flex>
                </v-layout>
		  <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-btn
                class="primary"
                :disabled="!formIsValid"
                type="submit">Request Checkup</v-btn>
            </v-flex>
          </v-layout>
		  </form>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  props: ['livestock']['users'],
  data () {
    return {
      stockName: '',
      vetName: '',
      date: new Date(),
      time: new Date(),
      itemsVetName: [
        { text: 'Bobby Magowan' },
        { text: 'Maria Kelly' },
        { text: 'Frank Ferran' },
        { text: 'Lisa Patterson' }
      ],
      itemsStock: [
        { text: 'Suffolk Sheep' },
        { text: 'Jersey Cows' },
        { text: 'Hampshire Pigs' }
      ]
    }
  },
  computed: {
    livestock () {
      return this.$store.getters.loadedLivestock
    },
    users () {
      return this.$store.getters.loadedUsers
    },
    formIsValid () {
      return this.stockName !== '' && this.vetName !== ''
    },
    submitDateTime () {
      const date = new Date(this.date)
      if (typeof this.time === 'string') {
        let hours = this.time.match(/^(\d+)/)[1]
        const minutes = this.time.match(/:(\d+)/)[1]
        date.setHours(hours)
        date.setMinutes(minutes)
      } else {
        date.setHours(this.time.getHours())
        date.setMinutes(this.time.getMinutes())
      }
      return date
    }
  },
  methods: {
    onCreateCheckup () {
      if (!this.formIsValid) {
        return
      }
      const checkupData = {
        stockName: this.stockName,
        vetName: this.vetName,
        date: this.submitDateTime,
        creatorId: this.creatorId
      }
      this.$store.dispatch('addCheckup', checkupData)
      this.$router.push('/viewcheckups')
    }
  }
}

  </script>



